import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';


class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  toggleCategories() {

    if(this.state.activeTab === 0){
      return(
        <div className="full-stack-projects-grid">
          {/* Project 1 */}
          <Card shadow={5} style={{minWidth: '600', margin: 'auto'}}>
            <CardTitle style={{color: '#fff', height: '300px', background: 'url(data:image/jpeg;base64,) center / cover'}} >subSet</CardTitle>
            <CardText>
              Designed a photo submission web app that translates pictures of handwritten java/cpp code into a savable, editable file along with a local compiler.
            </CardText>
            <CardActions border>
              <a href="https://github.com/ashleyswang/subset">Github</a>
            </CardActions>
            <CardMenu style={{color: '#fff'}}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>


      )
    } else if(this.state.activeTab === 1) {
      return (
        <div className="java-projects-grid">
          {/* Project 1 */}
          <Card shadow={5} style={{minWidth: '600', margin: 'auto'}}>
            <CardTitle style={{color: '#fff', height: '300px', background: 'url(data:image/jpeg;base64,) center / cover'}} >Craps</CardTitle>
            <CardText>
              Simulation of the classic Craps casino game that can be run in terminal
            </CardText>
            <CardActions border>
              <a href="https://github.com/evanaltshule/Programming-With-Java/tree/master/lab02">Github</a>
            </CardActions>
            <CardMenu style={{color: '#fff'}}>
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      )
    }

  }



  render() {
    return(
      <div>
        <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
          <Tab>React</Tab>
          <Tab>Java</Tab>
        </Tabs>


          <Grid>
            <Cell col={12}>
              <div className="content">{this.toggleCategories()}</div>
            </Cell>
          </Grid>


      </div>
    )
  }
}

export default Projects;